$general_bg: #0f0e06;
$general_fg: #cfbcba;
$modeline_in_bg: #36322f;
$modeline_in_fg: #887c8a;
$modeline_ac_bg: #692a12;
$modeline_ac_fg: #feeeca;
$cursor_bg: #ffaa33;
$cursor_fg: #0f0e06;

.Ymacs-Theme-ef-autumn { --ymacs-cursor-bg: #ffaa33; --ymacs-cursor-fg: #0f0e06; }
.Ymacs-Theme-ef-autumn { color: #cfbcba; background-color: #0f0e06; }
.Ymacs-Theme-ef-autumn .Ymacs_SplitCont > .bar { background-color: #969896; }
.Ymacs-Theme-ef-autumn .Ymacs_SplitCont.horiz > .bar { border-top: 1px solid #15140d; border-bottom: 1px solid #15140d; }
.Ymacs-Theme-ef-autumn .Ymacs_SplitCont.vert > .bar { border-left: 1px solid #15140d; border-right: 1px solid #15140d; }
.Ymacs-Theme-ef-autumn .Ymacs_Frame-active .Ymacs-current-line { background-color: #302a3a63 }
.Ymacs-Theme-ef-autumn .Ymacs-frame-content div.line:before { color: #887c8a; }
.Ymacs-Theme-ef-autumn .Ymacs_Frame-active div.line.Ymacs-current-line:before { color: #ffffff; font-weight: bold; }
.Ymacs-Theme-ef-autumn .Ymacs-frame-content:before { color: #887c8a; background-color: #0f0e06; }
.Ymacs-Theme-ef-autumn {
.Ymacs_Popup {
  --ymacs-popup-bg: #15140d;
  --ymacs-popup-fg: #cfbcba;
  --ymacs-popup-scrollbar-thumb: var(--ymacs-popup-fg);
}
.Ymacs_Popup .Ymacs_Menu {
.Ymacs_Menu_Item:hover, .Ymacs_Menu_Item.selected, .Ymacs_Menu_Item.selected:hover { background-color: #36213a; }
}
.Ymacs_Overlay.selection div { background-color: #3f1324; }
.Ymacs_Overlay.isearch div { color: #ffffff; background-color: #8f5040; }
.Ymacs_Overlay.isearch-lazy div { color: #ffffff; background-color: #4648d0; }
.Ymacs_Overlay.match-paren div { color: #000000; background-color: #32cd32; }
.Ymacs_Modeline { color: #887c8a; background-color: #36322f; }
.Ymacs_Frame-active .Ymacs_Modeline { color: #feeeca; background-color: #692a12; }
.minibuffer-prompt { color: #3dbbb0; }
.mode-line-buffer-id { font-weight: bold; }
.directive { color: #d570af; }
.type { color: #2fa526; }
.builtin { color: #ff7a7f; font-weight: bold; }
.function-name { color: #3dbbb0; }
.variable-name { color: #6fafff; }
.constant { color: #64aa0f; }
.string { color: #f06a3f; }
.string-starter { color: #f06a3f; }
.string-stopper { color: #f06a3f; }
.regexp { color: #f06a3f; }
.regexp-starter { color: #f06a3f; }
.regexp-stopper { color: #f06a3f; }
.regexp-modifier { color: #f06a3f; }
.keyword { color: #c48702; font-weight: bold; }
.comment { color: #cf9f7f; font-style: italic; }
.mcomment { color: #cf9f7f; font-style: italic; }
.comment-starter { color: #cf9f7f; font-style: italic; }
.mcomment-starter { color: #cf9f7f; font-style: italic; }
.mcomment-stopper { color: #cf9f7f; font-style: italic; }
.number { color: #64aa0f; }
.operator { }
.error { color: #c48702; }
.isearch-fail { color: #f06a3f; background-color: #461204; font-weight: bold; }
.xml-open-tag { color: #3dbbb0; }
.xml-close-tag { color: #3dbbb0; }
.xml-attribute { color: #6fafff; }
.xml-entity-starter { color: #64aa0f; }
.xml-entity-stopper { color: #64aa0f; }
.xml-entity { color: #64aa0f; }
.xml-open-bracket { }
.xml-close-bracket { }
.xml-closetag-slash { }
.xml-cdata { color: #cf9f7f; font-style: italic; }
.xml-cdata-starter { color: #cf9f7f; font-style: italic; }
.xml-cdata-stopper { color: #cf9f7f; font-style: italic; }
.lisp-keyword { color: #ff7a7f; font-weight: bold; }
.heading1 { color: #64aa0f; font-weight: bold; }
.heading2 { color: #ef656a; font-weight: bold; }
.heading3 { color: #3dbbb0; font-weight: bold; }
.heading4 { color: #df8f6f; font-weight: bold; }
.markdown-blockquote { color: #cf9f7f; font-style: italic; }
.markdown-blockquote1 { color: #cf9f7f; font-style: italic; }
.markdown-blockquote2 { color: #cf9f7f; font-style: italic; }
.markdown-blockquote3 { color: #cf9f7f; font-style: italic; }
}
